/* About.css */

/* Keep only About-specific styles here */
.about__container {
  row-gap: 2rem;
  text-align: center;
  margin-top: 100px;
  margin-bottom: 110px;
}

.about__subtitle {
  margin-bottom: var(--mb-2);
}

.about__img {
  justify-self: center;
}

.about__img img {
  width: 200px;
  border-radius: .5rem;
}

/* Work Experience and Education in About section */
.about__work, .about__education {
  margin-top: var(--mb-4);
  margin-bottom: var(--mb-4);
  text-align: left;
}

.about__work-title, .about__education-title {
  font-size: 1.25rem;
  color: var(--first-color);
  margin-bottom: var(--mb-2);
}

.about__work-item, .about__education-item {
  padding: 1rem;
  border-left: 3px solid var(--first-color);
  background-color: #f8f9fa;
  border-radius: 0 .5rem .5rem 0;
  margin-bottom: 1.5rem;
}

.about__work-item h4, .about__education-item h4 {
  margin-bottom: 0.5rem;
}

.about__work-company, .about__education-school {
  color: var(--first-color);
  margin-bottom: 0.5rem;
}

.about__work-date, .about__education-date {
  color: #6c757d;
  font-size: var(--smaller-font-size);
  margin-bottom: 1rem;
}

.about__work-list {
  margin-left: 1rem;
  margin-top: 1rem;
}

.about__work-list li {
  margin-bottom: 0.5rem;
  position: relative;
  padding-left: 1.5rem;
}

.about__work-list li i {
  position: absolute;
  left: 0;
  top: 0.25rem;
  color: var(--first-color);
}

