/* Contact.css */

/* Keep only Contact-specific styles here */
.contact__container {
  row-gap: 3rem;
}

.contact__content {
  display: flex;
  flex-direction: column;
  row-gap: 2rem;
}

.contact__subtitle {
  font-size: var(--h3-font-size);
  color: var(--first-color);
  margin-bottom: 1rem;
}

.contact__description {
  margin-bottom: 2rem;
}

/* Contact cards */
.contact__info {
  display: grid;
  grid-template-columns: 1fr;
  gap: 1.5rem;
}

.contact__card {
  background-color: white;
  padding: 1.5rem;
  border-radius: 0.75rem;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  transition: transform 0.3s ease;
}

.contact__card:hover {
  transform: translateY(-10px);
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.1);
}

.contact__card-icon {
  font-size: 2.5rem;
  color: var(--first-color);
  margin-bottom: 1rem;
}

.contact__card-title {
  font-size: var(--normal-font-size);
  font-weight: var(--font-semi);
  margin-bottom: 0.5rem;
}

.contact__card-data {
  display: block;
  margin-bottom: 1rem;
}

.contact__card-data a {
  color: var(--text-color);
  text-decoration: none;
}

.contact__button-small {
  color: var(--first-color);
  font-size: var(--small-font-size);
  font-weight: var(--font-semi);
  display: flex;
  align-items: center;
  transition: color 0.3s;
}

.contact__button-small:hover {
  color: var(--first-color-dark);
}

.contact__button-small i {
  font-size: 1rem;
  margin-left: 0.25rem;
  transition: transform 0.3s;
}

.contact__button-small:hover i {
  transform: translateX(5px);
}

/* Location */
.contact__location {
  display: flex;
  align-items: center;
  column-gap: 1.5rem;
  margin-bottom: 2rem;
  padding: 1.5rem;
  background-color: white;
  border-radius: 0.75rem;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.contact__location-icon {
  font-size: 2.5rem;
  color: var(--first-color);
}

.contact__location-title {
  font-size: var(--normal-font-size);
  font-weight: var(--font-semi);
  margin-bottom: 0.5rem;
}

/* Social links */
.contact__social {
  margin-bottom: 2rem;
}

.contact__social-title {
  font-size: var(--normal-font-size);
  font-weight: var(--font-semi);
  margin-bottom: 1rem;
}

.contact__social-links {
  display: flex;
  column-gap: 1.5rem;
}

.contact__social-icon {
  font-size: 2rem;
  color: var(--second-color);
  transition: all 0.3s ease;
}

.contact__social-icon:hover {
  color: var(--first-color);
  transform: translateY(-5px);
}

/* Resume section */
.contact__resume {
  background-color: white;
  padding: 1.5rem;
  border-radius: 0.75rem;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.contact__resume-title {
  font-size: var(--normal-font-size);
  font-weight: var(--font-semi);
  margin-bottom: 0.5rem;
}

.contact__resume-description {
  margin-bottom: 1.5rem;
}

.contact__resume-button {
  display: inline-flex;
  align-items: center;
  background-color: var(--first-color);
  color: white;
  padding: 0.75rem 1.5rem;
  border-radius: 0.5rem;
  font-weight: var(--font-semi);
  transition: background-color 0.3s;
}

.contact__resume-button:hover {
  background-color: var(--first-color-dark);
}

.contact__resume-button i {
  font-size: 1.25rem;
  margin-right: 0.5rem;
}

/* Adjustments for the modified contact layout */
@media screen and (min-width: 576px) {
  .contact__info {
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
  }

  .contact__card {
    padding: 2rem 1.5rem;
  }

  .contact__card-icon {
    font-size: 3rem;
    margin-bottom: 1.25rem;
  }
}

/* Adjust spacing between resume and social sections */
.contact__resume {
  margin-bottom: 2.5rem;
}

.contact__social {
  margin-top: 0;
}

/* Make social icons more prominent */
.contact__social-links {
  display: flex;
  gap: 1.5rem;
  justify-content: center;
}

.contact__social-icon {
  width: 50px;
  height: 50px;
  font-size: 1.75rem;
}

/* Improve location section */
.contact__location {
  padding: 1.75rem;
}

.contact__location-icon {
  font-size: 2.75rem;
}

.contact__location-title {
  font-size: 1.2rem;
}

@media screen and (max-width: 767px) {
  .contact__card {
    max-width: 300px;
    margin: 0 auto;
  }

  .contact__social-links {
    justify-content: flex-start;
  }
}